@media screen and (max-width: 1400px), screen and (max-device-width: 1400px) {
  body {
    font-size: 16px;
    line-height: 18px;
    .content {
      .container {
        grid-template-columns: 25% 1fr 20%;
        .content__popularProducts {
          ul {
            li {
              width: 230px;
              height: 290px;
            }
            li {
              img {
                width: 150px;
                height: 150px;
              }
            }
          }
        }
        .content__howToMakeOrder {
          div {
            .info {
              margin-left: 55px;
            }
          }
        }
      }
    }
    .contentAddres {
      iframe {
        height: 465px;
      }
      .contentAddres__contactForm {
        .comentary {
          input[type*="text"] {
            width: 184px;
          }
          textarea {
            width: 395px;
          }
        }
        .contacts {
          width: 75%;
        }
      }
    }
  }
}
